'use client';
import { useState, useEffect, useRef } from "react";
import { LeftColumn } from "./LeftColumn";
import { RightColumn } from "./RightColumn";
import EmailSender from "@/lib/emailsender";

export interface FormFields {
  companyName: string;
  contact: string;
  email: string;
  inflowPath: string;
  consultingUrl: string;
  requestKeyword: string;
  consultingArea: string;
  detailedInquiry: string;
}
export const QuotationForm = () => {
  const [activeStep, setActiveStep] = useState([true, false, false, false]);
  const [formData, setFormData] = useState<FormFields>({
    companyName: "",
    contact: "",
    email: "",
    inflowPath: "",
    consultingUrl: "",
    requestKeyword: "",
    consultingArea: "",
    detailedInquiry: "",
  });

  const formRef = useRef<HTMLFormElement>(null);

  const resetForm = () => {
    setFormData({
      companyName: "",
      contact: "",
      email: "",
      inflowPath: "",
      consultingUrl: "",
      requestKeyword: "",
      consultingArea: "",
      detailedInquiry: "",
    });
  };

  const { sendEmail } = EmailSender({ formData, resetForm });

  const [observer, setObserver] = useState<IntersectionObserver | null>(null);

  useEffect(() => {
    setObserver(new IntersectionObserver((entries) => {
      setActiveStep((prev) => {
        const newSteps = { ...prev };
        for (const entry of entries) {
          if (entry.isIntersecting) {
            const id = +entry.target.id;
            newSteps[id] = true;
          }
        }
        return newSteps;
      });
    }, {
      threshold: 1
    }));
  }, []);

  useEffect(() => {
    if (observer === null || formRef.current === null) return;

    const currentFormRef = formRef.current;

    for (const child of currentFormRef.children) {
      observer.observe(child);
    }

    return () => {
      if (currentFormRef) {
        for (const child of currentFormRef.children) {
          observer.unobserve(child.children[0]);
        }
      }
    };
  }, [observer]);

  return (
    <div className="flex w-full h-screen overflow-hidden">
      <div className="flex-none w-2/4 pr-3 pl-8 pt-16 overflow-hidden">
        <LeftColumn activeStep={activeStep} />
      </div>
      <div className="flex-auto pr-3 pl-3 pt-16 overflow-y-auto">
        <RightColumn
          formRef={formRef}
          formData={formData}
          setFormData={setFormData}
          sendEmail={sendEmail}
        />
      </div>
    </div>
  );
};
