import React, { Dispatch, SetStateAction } from 'react'
import { useTranslation } from '@/lib/i18n/client';
import { FormFields } from './Form';


interface DetailedInquiryFormProps {
  formData: FormFields;
  setFormData: Dispatch<SetStateAction<FormFields>>;
}

export const DetailedInquiryForm: React.FC<DetailedInquiryFormProps> = ({ formData, setFormData, }) => {
  const { t } = useTranslation('en', 'namespace');
  return (
    <div className="space-y-6 p-4 md:p-7">
      <div className="mb-8">
        <h3 className="text-lg md:text-xl mb-4">04</h3>
        <h4 className="font-medium mb-6">{t('detailinquiry_header')}</h4>
      </div>

      <div>
        <textarea
          placeholder={t('detailinquiry_placeholder')}
          value={formData.detailedInquiry}
          onChange={(e) => setFormData({ ...formData, detailedInquiry: e.target.value })}
          className="w-full h-48 p-4 border border-gray-300 rounded-lg 
            placeholder:text-gray-400 focus:outline-none focus:ring-1 
            focus:ring-blue-500 resize-none"
        />
      </div>
    </div>
  )
}