import React, { Dispatch, SetStateAction } from 'react';
import { useTranslation } from '@/lib/i18n/client';
import { FormFields } from './Form';

interface InquiryFormProps {
  formData: FormFields;
  setFormData: Dispatch<SetStateAction<FormFields>>;
}

export const InquiryForm: React.FC<InquiryFormProps> = ({ formData, setFormData }) => {
  const { t } = useTranslation('en', 'namespace');
  return (
    <div className="space-y-6 p-4 md:p-7">
      <div className="mb-8">
        <h3 className="text-lg mb-4">03</h3>
        <h4 className="font-medium mb-6">{t('inquiry_header')}</h4>
      </div>

      {/* Site/Place Selection */}
      <div className="mt-6">
        <label className="flex items-center gap-1 mb-2">
          {t('inquiry_siteplace_label')}
          <span className="text-yellow-400">*</span>
        </label>
        <div className="grid grid-cols-2 gap-4">
          <button
            type="button"
            onClick={() => setFormData({ ...formData, consultingArea: 'site' })}
            className={`p-4 text-center border rounded-md transition-colors
                  ${formData.consultingArea === 'site'
                ? 'border-blue-500 bg-blue-50'
                : 'border-gray-300 hover:border-blue-500'}`}
          >
            {t('inquiry_site')}
          </button>
          <button
            type="button"
            onClick={() => setFormData({ ...formData, consultingArea: 'place' })}
            className={`p-4 text-center border rounded-md transition-colors
                  ${formData.consultingArea === 'place'
                ? 'border-blue-500 bg-blue-50'
                : 'border-gray-300 hover:border-blue-500'}`}
          >
            {t('inquiry_place')}
          </button>
        </div>
      </div>
      {/* URL Input */}
      <div className="mt-10">
        <label className="flex items-center gap-1 mt-3">
          {t('inquiry_url')}
          <span className="text-yellow-400">*</span>
        </label>
        <input
          type="url"
          placeholder="ex) https://site-high.com/"
          value={formData.consultingUrl}
          onChange={(e) => setFormData({ ...formData, consultingUrl: e.target.value })}
          className="w-full mt-1 p-2 border border-gray-300 rounded-md focus:outline-none focus:ring-1 focus:ring-blue-500"
        />
      </div>

      {/* Request Keyword Input */}
      <div className="mt-6">
        <label className="flex items-center gap-1">
          {t('inquiry_inputkeyword_label')}
          <span className="text-yellow-400">*</span>
        </label>
        <input
          type="text"
          placeholder={t('inquiry_inputkeyword_placeholder')}
          value={formData.requestKeyword}
          onChange={(e) => setFormData({ ...formData, requestKeyword: e.target.value })}
          className="w-full mt-1 p-2 border border-gray-300 rounded-md focus:outline-none focus:ring-1 focus:ring-blue-500"
        />
      </div>
    </div>
  )
}
