import React, { Dispatch, SetStateAction } from "react";
import { useTranslation } from "@/lib/i18n/client";
import { FormFields } from "./Form";

interface InformationFormProps {
  formData: FormFields;
  setFormData: Dispatch<SetStateAction<FormFields>>;
}

export const InformationForm: React.FC<InformationFormProps> = ({ formData, setFormData }) => {
  const { t } = useTranslation('en', 'namespace');
  return (
    <div className="space-y-6 p-4 md:p-7">
      <h3 className="text-lg md:text-xl mb-2">01</h3>
      <h4 className="font-medium mb-4">{t('inform_head')}</h4>
      <p className="text-sm text-gray-600 mb-6">
        {t('inform_note')}
      </p>

      <div className="space-y-4">
        {["companyName", "contact", "email"].map((field) => (
          <div key={field} className="flex items-center border border-gray-300 p-3 rounded-lg">
            <label className="min-w-[120px] mr-4">
              {t(field)}<span className="text-yellow-400">*</span>
            </label>
            <input
              type={field === "email" ? "email" : "text"}
              placeholder={`ex) ${field === "email" ? "example@domain.com" : "Smart Pickin"}`}
              value={formData[field as keyof FormFields]}
              onChange={(e) => setFormData({ ...formData, [field]: e.target.value })}
              className="flex-1 p-2 border-none rounded-lg text-gray-600 placeholder-gray-400 focus:outline-none focus:ring-0"
              style={{ width: '100%' }}
              required
            />
          </div>
        ))}
      </div>
    </div>
  );
};