'use client';
import { Checkbox } from "@headlessui/react";
import { Dispatch, SetStateAction, useState } from "react";
import { Button } from "@headlessui/react";
import { InformationForm } from "./InformationForm";
import { Inflowpath } from "./Inflowpath";
import { InquiryForm } from "./InquiryForm";
import { DetailedInquiryForm } from "./DetailedInquiryForm";
import { useTranslation } from "@/lib/i18n/client";
import { FormFields } from "./Form";

interface RightColumnProps {
  formRef: React.RefObject<HTMLFormElement | null>;
  formData: FormFields;
  setFormData: Dispatch<SetStateAction<FormFields>>;
  sendEmail: (e: React.FormEvent<HTMLFormElement>) => Promise<void>;
}

export const RightColumn: React.FC<RightColumnProps> = ({ formRef, formData, setFormData, sendEmail }) => {
  const { t } = useTranslation('en', 'namespace');
  const [enabled, setEnabled] = useState(false);


  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    await sendEmail(e);
  };
  return (
    <form onSubmit={handleSubmit} ref={formRef} className="flex flex-col h-full p-4">
      <div className="px-4 mt-7">
        <div className="flex items-center gap-2 mb-3">
          <div className="w-5 h-5 rounded-full bg-yellow-400 flex items-center justify-center text-white">
            !
          </div>
          <span className="font-medium">{t('inquiry_guide')}</span>
        </div>
        {/* Inquiry Guide Box */}
        <div className="bg-blue-50 rounded-lg p-3 flex-shrink-0">
          <p className="text-sm text-gray-600 mb-2">
            {t('rightboxnote_one')}
          </p>
          <p className="text-sm text-gray-600">
            {t('rightboxnote_two')}
          </p>
        </div>
      </div>

      {/* Form Sections */}
      <div className="space-y-6 pb-19 py-12 mt-6 flex-1">
        <div className="form-section min-h-section mt-section-gap" id="01">
          <InformationForm formData={formData} setFormData={setFormData} />
        </div>
        <div className="form-section min-h-section mt-section-gap" id="02">
          <Inflowpath formData={formData} setFormData={setFormData} />
        </div>
        <div className="form-section min-h-section mt-section-gap" id="03">
          <InquiryForm formData={formData} setFormData={setFormData} />
        </div>
        <div className="form-section min-h-section mt-section-gap" id="04">
          <DetailedInquiryForm formData={formData} setFormData={setFormData} />
        </div>
      </div>
      <div className="space-y-6 p-4 md:p-7">
        <div className="flex items-center">
          <Checkbox
            checked={enabled}
            onChange={setEnabled}
            className="group block size-7 rounded border bg-white data-[checked]:bg-blue-500"
          >
            {/* Checkmark icon */}
            <svg
              className="stroke-white opacity-0 group-data-[checked]:opacity-100"
              viewBox="0 0 14 14"
              fill="none"
            >
              <path
                d="M3 8L6 11L11 3.5"
                strokeWidth={2}
                strokeLinecap="round"
                strokeLinejoin="round"
              />
            </svg>
          </Checkbox>

          <label className="ml-2 text-gray-700">
            Collection and use of personal information. I agree with that.
          </label>
        </div>
        <div>
          <Button
            type="submit"
            className="border border-blue-500 rounded h-16 w-full text-lg text-blue-500 hover:bg-blue-500 hover:text-white active:bg-blue-700">
            Submit an inquiry
          </Button>
        </div>
      </div>
    </form>
  );
};

