import emailjs from '@emailjs/browser';

interface FormData {
  companyName: string;
  email: string;
  contact: string;
  inflowPath: string;
  consultingUrl: string;
  requestKeyword: string;
  consultingArea: string;
  detailedInquiry: string;
}

const EmailSender = ({ formData, resetForm }: { formData: FormData; resetForm: () => void }) => {
  const sendEmail = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    
    try {
      emailjs.init("kgFykzhnzo2PWHj4H");

      const response = await emailjs.send(
        "service_3oxeb5f", 
        "template_g0xqtw8",
        {
          to_name: "Admin",
          from_name: formData.companyName,
          from_email: formData.email,
          contact: formData.contact,
          inflow_path: formData.inflowPath,
          consulting_url: formData.consultingUrl,
          request_keyword: formData.requestKeyword,
          consulting_area: formData.consultingArea,
          detailed_inquiry: formData.detailedInquiry,
        },
        "kgFykzhnzo2PWHj4H"
      );

      if (response.status === 200) {
        alert("Email sent successfully!");
        resetForm();
      }
    } catch (error) {
      console.error("Error sending email:", error);
      alert("Failed to send email. Please try again.");
    }
  };

  return { sendEmail };
};

export default EmailSender;